.experience-main {
    /* Add styles for the main container if needed */
}

.experience-head {
    display: flex;
    width: 50vh;
    flex-direction: row;
    justify-content: flex-start;
    align-items: baseline;
    gap: 10px;
}

.experience-dot {
    background-color: #f2f2f2;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    box-shadow: 0 0 20px #fff;
    color: var(--on-background);
}

.experience-overline {
    color: rgba(242, 242, 242, .4);
    text-align: left;
    letter-spacing: 1.5px;
    margin-top: 1px;
    font-family: Neue Montreal, sans-serif;
    font-size: 11px;
    font-weight: 500;
    line-height: 19px;
}

.exprience-bod {
    margin-top: 5vh;
}

.grid-container {
    display: grid;
    grid-template-columns: auto auto;
    column-gap: 10vw;
}

.grid-item {
    font-size: 40px;
}

.experience-heading {
    letter-spacing: -.1px;
    text-shadow: 0 0 20px rgba(255, 255, 255, .2), 0 -6px 15px rgba(255, 90, 0, .1), 0 5px 15px rgba(0, 87, 255, .25);
    font-family: Neue Montreal, sans-serif;
    font-size: 40px;
    font-weight: 500;
    line-height: 125%;
}

.experience-heading.info-experience {
    font-family: Neue Montreal, sans-serif;
    font-weight: 500;
}

.experience-block {
    grid-row-gap: 20px;
    flex-direction: column;
    display: flex;
    align-items: flex-start;
}

.experience-subheading {
    letter-spacing: .1px;
    flex: 0 auto;
    padding-right: 24px;
    font-family: Neue Montreal, sans-serif;
    font-size: 24px;
    font-weight: 500;
    line-height: 125%;
}

.experience-subheading.info {
    font-family: Neue Montreal, sans-serif;
}

.experience-body {
    color: rgba(242, 242, 242, .6);
    letter-spacing: .4px;
    flex: 1;
    font-family: Neue Montreal, sans-serif;
    font-size: 16px;
    font-weight: 300;
    line-height: 150%;
}

.experience-body.info {
    letter-spacing: .4px;
}

.text-info-meta {
    color: rgba(242, 242, 242, .4);
    font-family: Neue Montreal, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 125%;
}